import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.components.animation.registerKeyframes
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { org.example.testproject.pages.HomePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerComponentStyle(org.example.testproject.styles.NavigationHeaderStyle)
        ctx.theme.registerComponentStyle(org.example.testproject.styles.ButtonStyle)
        ctx.theme.registerComponentStyle(org.example.testproject.styles.SocialLinkStyle)
        ctx.theme.registerComponentStyle(org.example.testproject.styles.PresentationStyle)
        ctx.theme.registerComponentStyle(org.example.testproject.styles.SkillStyle)
        ctx.theme.registerComponentStyle(org.example.testproject.styles.AcademicStyle)
        ctx.theme.registerComponentStyle(org.example.testproject.styles.PortfolioStyle)
        ctx.theme.registerComponentStyle(org.example.testproject.styles.FormStyle)
        ctx.theme.registerComponentStyle(org.example.testproject.styles.LogoStyle)
        ctx.theme.registerComponentVariants(org.example.testproject.styles.CircleButtonVariant)
        ctx.stylesheet.registerKeyframes(org.example.testproject.styles.AppearMoveKeyFrames)
        ctx.stylesheet.registerKeyframes(org.example.testproject.styles.ShowKeyFrames)
    }

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    AppGlobals.initialize(mapOf("title" to "TestProject"))
    renderComposable(rootElementId = "root") {
        org.example.testproject.AppEntry {
            router.renderActivePage { renderWithDeferred { it() } }
        }
    }
}
